* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   font-family: "Rubik", sans-serif;
}

:root {
   --color-bg: #def5e5;
   --color-accent: #bcead5;
   --color-link: #9ed5c5;
   --color-heading: #8ec3b0;
   --color-text: #333;
}

/* App background styling */
.App {
   background-color: var(--color-bg);
   background-image: url("https://www.transparenttextures.com/patterns/cubes.png");
   background-repeat: repeat;
   min-height: 100vh;
   position: relative;
   z-index: 0;
}

body {
   background-color: var(--color-bg);
   color: var(--color-text);
   font-family: Arial, sans-serif;
   margin: 0;
   padding: 0;
}

h1,
h2,
h3 {
   color: var(--color-heading);
}

a {
   color: var(--color-link);
   text-decoration: none;
}

a:hover {
   color: var(--color-accent);
}

button {
   background-color: var(--color-link);
   color: white;
   border: none;
   padding: 10px 20px;
   border-radius: 6px;
   cursor: pointer;
}

button:hover {
   background-color: var(--color-accent);
}

section {
   opacity: 0;
   transform: translateY(20px);
   transition: all 1s ease;
   padding: 60px 20px;
   text-align: center;
}

section.visible {
   opacity: 1;
   transform: translateY(0);
}

@media (max-width: 768px) {
   section {
      padding: 40px 15px;
   }

   h1,
   h2,
   h3 {
      font-size: 1.5rem;
   }

   .project-grid {
      grid-template-columns: 1fr;
   }

   button {
      padding: 8px 16px;
      font-size: 0.9rem;
   }
}

@media (max-width: 1024px) {
   section {
      padding: 50px 20px;
   }

   h1,
   h2,
   h3 {
      font-size: 1.8rem;
   }

   button {
      padding: 9px 18px;
      font-size: 1rem;
   }

   .project-grid {
      grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
   }
}

@media (max-width: 480px) {
   section {
      padding: 30px 10px;
   }

   h1,
   h2,
   h3 {
      font-size: 1.2rem;
   }

   button {
      padding: 6px 12px;
      font-size: 0.8rem;
   }
}
