.about {
   background-color: #fbfbfb; /* soft green from your palette */
   background-image: url("https://www.transparenttextures.com/patterns/cubes.png");
   background-repeat: repeat;
   padding: 60px 20px;
   text-align: center;
}

.about h2 {
   color: var(--color-heading);
   margin-bottom: 20px !important;
}

.about p {
   color: var(--color-text);
   max-width: 700px;
   margin: 0 auto;
   font-size: 1.4rem;
}

@media (max-width: 768px) {
   .about h2 {
      font-size: 1.6rem !important;
   }

   .about p {
      font-size: 1rem;
   }
   .about p {
      line-height: 1.6;
   }
   .about {
      padding: 30px 10px;
   }
}

@media (max-width: 480px) {
   .about h2 {
      font-size: 1.4rem !important;
   }

   .about p {
      font-size: 0.9rem;
   }
   .about p {
      line-height: 1.6;
   }
   .about {
      padding: 20px 5px;
   }
}
