.hero {
   height: 100vh;
   display: flex;
   flex-direction: row;
   justify-content: center;
   align-items: center;
   text-align: center;
   padding: 0 30px;
   gap: 60px;
   opacity: 0;
   transform: translateX(-50px);
   transition: all 1s ease;
}

.hero.fade-in {
   opacity: 1;
   transform: translateX(0);
   transition: opacity 1s ease, transform 1s ease;
}

.hero-intro {
   font-size: 3rem;
   font-weight: 700;
}

.hero p {
   margin-top: 15px;
   margin-bottom: 10px;
   font-size: 1.2rem;
   color: #333333;
   font-weight: 500;
}

.hero-content {
   flex: 0 1 auto;
   max-width: 45%;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   gap: 10px;
   padding-top: 30px;
   padding-bottom: 30px;
   min-height: 10rem; /* reserve vertical space */
   min-width: 35rem; /* reserve horizontal space */
}

.hero-image {
   flex: 0 1 auto;
   display: flex;
   justify-content: center;
   min-width: 22rem; /* keep space reserved for the image */
   margin-left: 20px; /* push slightly to the right */
}

.hero-image img {
   width: 20rem;
   height: 20rem;
   max-width: 100%;
   object-fit: cover;
   border-radius: 50%;
   flex-shrink: 0;
   flex-grow: 0;
}

@media (max-width: 768px) {
   .hero {
      /* flex-direction: column; */
      flex-direction: column-reverse;
      gap: 20px;
      text-align: center;
   }
   .hero-content {
      max-width: 90%;
      min-width: auto;
      padding-top: 10px;
      padding-bottom: 10px;
   }
   .hero-image img {
      width: 10rem;
      height: 10rem;
   }
   .hero-content h1 {
      font-size: 1.5rem;
   }
   .hero-content p {
      font-size: 1rem;
   }
}

@media (max-width: 480px) {
   .hero-image img {
      width: 10rem;
      height: 10rem;
   }

   .hero-content h2 {
      font-size: 1rem;
   }
}
